.login-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
  }
  
  @media (max-width: 600px) {
    .login-container {
      padding: 20px;
      box-shadow: none;
    }
  }